* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.slider {
  position: relative;
  height: 80vh;
  width: 90vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image {
  width: 100%;
  justify-content: center;
  align-items: center;
  box-shadow: -1px 1px 13px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px 1px 13px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 1px 13px 0px rgba(0, 0, 0, 0.75);
  border-radius: 10px;
}

.right-arrow {
  position: absolute;
  top: 70vh;
  right: 4vw;
  order: 3;
  font-size: 5rem;
  color: grey;
  cursor: pointer;
  z-index: 10;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.1s all ease-in-out;
}

.left-arrow {
  position: absolute;
  top: 70vh;
  left: 4vw;
  font-size: 5rem;
  color: grey;
  cursor: pointer;
  z-index: 10;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: 0.1s all ease-in-out;
}

.right-arrow:hover {
  transform: scale(1.2);
  color: rgb(205, 204, 204);
}

.left-arrow:hover {
  transform: scale(1.2);
  color: rgb(205, 204, 204);
}

.slide {
  opacity: 0;
  transition-duration: 0.5s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 0.5s;
  transform: scale(1.08);
}

@media screen and (min-width: 1024px) {
  .slider {
    height: 100vh;
    width: 60vw;
  }
  .image {
    width: 100%;
  }
  .right-arrow {
    top: 50vh;
    color: white;
    font-size: 3rem;
    right: 0vw;
  }
  .left-arrow {
    top: 50vh;
    color: white;
    font-size: 3rem;
    left: 0vw;
  }
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
}/*# sourceMappingURL=main.css.map */