@include media-md {
  .slider {
    height: 100vh;
    width: 60vw;
  }
  .image {
    width: 100%;
  }

  .right-arrow {
    top: 50vh;
    color: white;
    font-size: 3rem;
    right: 0vw;
  }
  .left-arrow {
    top: 50vh;
    color: white;
    font-size: 3rem;
    left: 0vw;
  }
}
